<template>
<nav>
  <router-link to="/add" class="home register">Add image</router-link>
  <router-link to="/" class="home register">Home</router-link>
</nav>
</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'

export default {

  data() {
    return {
      categories: '',
      error: false,
      coords: [],
      uploadedImage: ''
    }
  },
  mounted() {
    this.$nextTick(function () {
      ymaps.ready(() => {
        const myMap = new ymaps.Map('map', {
          center: [53.893009, 27.567444],
          zoom: 10
        });
        myMap.events.add('click', (e) => {
          if (!myMap.balloon.isOpen()) {
            this.coords = e.get('coords');
            myMap.balloon.open(this.coords, {
              contentHeader: 'Photo was taken here!',
              contentBody:
                '<p>Coordinates: ' + [
                  this.coords[0].toPrecision(6),
                  this.coords[1].toPrecision(6)
                ]
            });
          } else {
            myMap.balloon.close();
          }
        })
      })
    })
  },
  methods: {
    UploadImage() {
      const file = document.querySelector('input[type=file]').files[0]
      const reader = new FileReader()
      reader.onloadend = () => {
        this.uploadedImage = reader.result;
      }
      reader.readAsDataURL(file);
      this.uploadedImage = reader.result;
    },
    SaveFile(){
      console.log(this.uploadedImage)
      const user = JSON.stringify({base64Image: this.uploadedImage, lat: this.coords[0], lng: this.coords[1], categories: this.categories.split(',')})
      const customConfig = {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      }
      axios.post('/api/image', user, customConfig).then(response => {
        const temp = response.data
      }).catch((error) => {
        this.error = true
        console.log(error.response.status)
      })

}
  }
}
</script>

<style>
#map {
  width: 600px;
  height: 400px;
  margin-left: 35%;
}

.info_paragraph {
  font-size: 25px;
}
</style>
